import React from "react";
import sps from '../../assets/sps.png'
import takenote from '../../assets/takenote.png'
import techblog from '../../assets/techblog.png'
import hexgen from '../../assets/hex-gen.png'
import jolt from '../../assets/Jolt.png'
import template1 from '../../assets/template1.png'
import { Typography, Container, Paper, Card, CardMedia, CardContent, CardActions, Button } from "@mui/material";
import IconButton from '@mui/material/IconButton';
import InfoIcon from '@mui/icons-material/Info';
import lol from '../../assets/lol.jpg'
import food from '../../assets/food.png'
import farts from '../../assets/farts.jpg'
import '../../styles.css'

const itemData = [
    { 
        title: 'ColorScape Collective',
        img: template1,
        link: 'https://gh-pages.d2m9wsygth49f2.amplifyapp.com/',
        tech: 'Vite, React'
    },
    {
        title: 'Save the Princess Sim',
        img: sps,
        link: 'https://sheltered-falls-73233.herokuapp.com/',
        tech: 'Express, SQL, JS'
    },
    {
        title: 'Take Note',
        img: takenote,
        link: 'https://floating-bastion-25491.herokuapp.com/',
        tech: 'Node, FS'
    },
    {
        title: 'Tech Blogs',
        img: techblog,
        link: 'https://powerful-woodland-43531.herokuapp.com/',
        tech: 'Seqelize, Express'
    },
    {
        title: 'Hex Color Gen',
        img: hexgen,
        link:'https://clarktr1.github.io/Hex-Generator/',
        tech: 'React'

    },
    { 
        title: 'Jolt Banking Solution',
        img: jolt,
        link: 'https://jolt2.herokuapp.com/',
        tech: 'MERN, Plaid'
    },
    {
        title: 'League of Legends Database',
        img: lol,
        link: 'https://clarktr1.github.io/league-tracker/',
        tech: 'React, RIOT API'
    },
    { 
        title: 'Chef Food Restaurant Browser',
        img: food,
        link: 'https://springrestaurant.netlify.app/',
        tech: 'React, Spring Boot'
    },
    {
        title: 'Frisco Community Band',
        img: farts,
        link: 'https://friscocommunityband.org',
        tech: 'SquareSpace, JS, CSS'
    }

   
]


function ProjectList() {
    return (
        <Container sx={{ backgroundColor: '#1e1e1e', borderRadius: '8px', minHeight:"100vh"}} id="project">
        <Paper sx={{ backgroundColor: '#3664f8', borderRadius: '8px', marginBottom: '2rem' }}>
            <Typography component='h2' sx={{ textAlign: 'center', fontSize: '3rem', color: '#fff', fontWeight: '600', padding: '1.2rem' }}>My Work</Typography>
        </Paper>
        <Container sx={{ display: 'flex', justifyContent: 'center', flexWrap: 'wrap' }}>
            {itemData.map((item) => (
                <Card key={item.title} sx={{ maxWidth: 345, height:400, margin: '1rem', position:"relative" }}>
                    <CardMedia
                        component="img"
                        height="240"
                        image={item.img}
                        alt={item.title}
                    />
                    <CardContent>
                        <Typography gutterBottom variant="h5" component="div">
                            {item.title}
                        </Typography>
                        <Typography variant="body2" color="text.secondary">
                            {item.tech}
                        </Typography>
                    </CardContent>
                    <CardActions sx={{backgroundColor:"#000", position:"absolute", bottom:0, width:"100%"}}>
                        <IconButton
                            sx={{ color: 'rgba(255, 255, 255, 0.54)' }}
                            aria-label={`info about ${item.title}`}
                            href={item.link}
                            target="_blank"
                        >
                            <InfoIcon />
                        </IconButton>
                        <Button size="small" href={item.link} target="_blank">Learn More</Button>
                    </CardActions>
                </Card>
            ))}
        </Container>
    </Container>

    )
}

export default ProjectList
