import React from "react";
import { TagCloud } from "@frank-mayer/react-tag-cloud";

const Sphere1 = () => (
	<TagCloud
		options={(w: Window & typeof globalThis): any => ({
            radius:140,
			maxSpeed: "medium",
		})}
		onClickOptions={{ passive: true }}
	>
		{[
		"Java",
        "Spring Boot",
        "JavaScript",
        "React",
        "Sequelize",
        "MySQL",
        "NodeJS",
        "GIT",
        "AWS",
        "Material UI",
        "Express",
        "Agile",
        "NextJS"
		]}
	</TagCloud>
);
export default Sphere1;